<template>
  <v-data-table
    @pagination="changePaginate"
    :items="orders"
    :headers="headers"
    :server-items-length="serverItemsLength"
    :page="paginate.hasOwnProperty('page') ? paginate.page : 1"
    class="elevation-4"
  >
    <template v-slot:top>
      <v-row>
        <v-col cols="12" md="6">
          <v-text-field
            v-model="filter.id"
            label="Номер заказа"
            type="number"
            class="mx-4"
          ></v-text-field>
        </v-col>
        <v-col cols="12" md="6">
          <div class="d-flex align-center mx-4">
            <v-select
              v-model="filter.status"
              :items="statuses"
              label="Статусы заказа"
              item-value="name"
              item-text="name"
            />
            <v-btn
              :disabled="!(filter.id || filter.status)"
              @click="resetFilter"
              class="ml-2"
              fab
            >
              <v-icon>
                mdi-close-circle-outline
              </v-icon>
            </v-btn>
          </div>
        </v-col>
      </v-row>
    </template>

    <template v-slot:item.id="{ item }">
      {{ item.id }}
    </template>
    <template v-slot:item.user="{ item }">
      {{ item.user.information.first_name }}
    </template>
    <template v-slot:item.status="{ item }">
      {{ item.status.name }}
    </template>
    <template v-slot:item.total_sum="{ item }">
      {{ item.total_cash + " руб." }}
    </template>
    <template v-slot:item.actions="{ item }">
      <v-icon small class="mr-2" @click="editItem(item.id)">
        mdi-pencil
      </v-icon>
      <v-icon small @click="deleteItem(item)">
        mdi-delete
      </v-icon>
    </template>
  </v-data-table>
</template>

<script>
export default {
  name: "OrdersTable",
  data() {
    return {
      headers: [
        {
          text: "Номер",
          align: "start",
          sortable: false,
          value: "id"
        },
        {
          text: "Клиент",
          sortable: false,
          value: "user"
        },
        {
          text: "Статус заказа",
          sortable: false,
          value: "status"
        },
        {
          text: "Итоговая сумма",
          sortable: false,
          value: "total_sum"
        },
        {
          text: "Действия",
          sortable: false,
          value: "actions"
        }
      ],
      filter: {
        id: null,
        status: null
      },
      paginate: {}
    };
  },
  methods: {
    editItem(id) {
      this.$emit("toggleEdit", id);
    },
    changeSearchParams({ paginate, filter }) {
      this.$emit("changeSearchParams", { paginate, filter });
    },
    resetFilter() {
      this.filter.status = this.filter.id = null;
    },
    changePaginate(paginateData) {
      this.paginate = paginateData;

      // Осуществляет вызов
      this.changeSearchParams({
        paginate: this.paginate,
        filter: this.filter
      });
    }
  },
  props: {
    orders: {
      type: Array,
      required: true
    },
    statuses: {
      type: Array,
      required: true
    },
    serverItemsLength: {
      type: Number,
      required: true
    }
  },
  watch: {
    filter: {
      deep: true,
      handler(filter) {
        this.paginate.page = 1;
        this.changeSearchParams({
          paginate: this.paginate,
          filter
        });
      }
    }
  }
};
</script>

<style scoped></style>
